.lesson {
  height: 100%;
}
.lesson h1 {
  height: 15%;
}
.lesson svg {
  height: 100%;
}

.lesson-learning {
  height: 90%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.lesson-question {
  height: 90%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.lesson-map {
  width: auto;
  min-height: 85%;
  height: 85%;
  max-height: 100%;
  background-color: rgb(141, 189, 252);
  transition: all 0.5s;
}

.map-details {
  max-width: 40%;
  width: 40%;
  min-width: 30%;
  display: flex;
  flex-direction: column;
}
.map-details h1 {
  margin: 0;
}
.map-details p {
  margin: 0;
}
.map-details img {
  height: 100px;
  width: auto;
  max-width: 100%;
  object-fit: contain;
}

