.circle {
  width: 80px;
  height: 80px;
  background-color: green;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}
.circle img {
  height: 70%;
  pointer-events: none;
  user-select: none;
}

.circle:active {
  transform: scale(0.95);
}

.circle:hover {
  cursor: pointer;
}

.test {
  background-color: blue;
  top: 100%;
  position: absolute;
  max-width: 350%;
  margin-left: 10px;
  margin-top: 10px;
}

.game-element-cont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  align-items: center;
}

.center {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.game-element {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 140px;
  margin-left: 10px;
}
.game-element p {
  text-align: center;
  max-height: 60px;
  height: 50px;
  margin: 0;
  margin-bottom: 7px;
  display: flex;
  align-items: center;
}

.c48 {
  width: 48%;
}

.selection-section {
